.roboto-medium {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

html {
  --blue-color: #00acdc;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  color: #000;
  margin-left: 20px;
  list-style: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__item :hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

